<template>
  <div class="gear">
    <img src="../assets/gear.png" alt=""  class="RotateAW" :class="{'paused': paused}">
  </div>
</template>

<script>
export default {
  name: 'gear',
  props: {
    paused: {
      type: Boolean,
      default: false
    }
  }

}
</script>

<style scoped>
.gear img{
  width: 250px;
  height: 250px;
}
.RotateAW{
  animation: RotateAW  5s linear infinite;
}
.paused{
  animation-play-state:paused;
}
@keyframes RotateAW{
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}

</style>



